@import 'styles/variables/_index.scss';
@import 'styles/mixins/_index.scss';

// Карточка продукта
.Product {
    $root: &;

    display: flex;
    flex-direction: column;
    font-family: 'TrebuchetMS', sans-serif;

    // Контент
    &__content {
        display: flex;
        position: relative;
        margin-bottom: 14px;
        overflow: hidden;

        &:hover {
            cursor: pointer;
        }
    }

    // Основная информация
    &__info {
        position: absolute;
        top: 20px;
        left: 50px;
        right: 50px;
        z-index: 2;
    }

    // Текст
    &__text {
        color: $grey;
        line-height: 16px;

        &--size {
            &-sm {
                font-size: 14px;
            }
        }

        &--weight {
            &-bold {
                font-family: 'TrebuchetMS Bold', sans-serif;
            }
        }

        &--color {
            &-pink {
                color: #{$pink};
            }
        }
    }

    &__title, &__subtitle {
        color: #000000;
        font-family: 'TrebuchetMS Bold', sans-serif;
    }

    // Заголовок
    &__title {
        margin-top: 8px;
        font-size: 48px;
        line-height: 53px;
    }

    // Подзаголовок
    &__subtitle {
        font-size: 24px;
    }

    // Детали
    &__details {
        display: flex;
        flex-direction: column;
        margin-top: 15px;
    }

    // Изображение
    &__imageWrap {
        position: absolute;
        left: 4px;
        right: 4px;
        bottom: 4px;
        border-bottom-left-radius: 8px;
        overflow: hidden;
    }

    &__image {
        transform: translate(-27px, 105px);
    }

    // Вес
    &__weight {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: absolute;
        right: 15px;
        bottom: 15px;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background-color: $blue;
    }

    &__weightCount {
        font-size: 42px;
        line-height: 35px;
    }

    &__weightUnit {
        margin-top: 3px;
        font-family: 'TrebuchetMS Bold', sans-serif;
        font-size: 22px;
        line-height: 18px;
    }

    // Описание
    &__description {
        color: #FFFFFF;
        font-size: 13px;
        text-align: center;
        
        &--color {
            &-blue {
                color: #{$blue};
            }
        }

        &--action {
            position: relative;

            &::after {
                content: '';
                position: absolute;
                top: 100%;
                left: 0;
                width: 100%;
                border-bottom: 1px dashed #{$blue};
            }

            &:hover {
                cursor: pointer;
            }
        }
    }

    // Модификаторы
    &--selected {
        #{$root}__weight {
            background-color: #{$pink};
        }

        #{$root}__background {
            path {
                stroke: #{$pink};
            }
        }
    }

    &--disabled {
        #{$root}__content {
            pointer-events: none;
        }

        #{$root}__info {
            opacity: 0.25;
        }

        #{$root}__image {
            opacity: 0.40;
        }

        #{$root}__weight {
            background-color: #{$grey-light};
        }

        #{$root}__background {
            path {
                stroke: #{$grey-light};
            }
        }

        #{$root}__description {
            color: $yellow;
        }
    }

    // Медиа запросы
    @include screen-3 {
        &__background {
            width: 280px;
            height: 420px;
        }

        &__text {
            font-size: 14px;
        }

        &__title {
            font-size: 30px;
            line-height: 32px;
        }

        &__subtitle {
            font-size: 19px;
        }

        &__image {
            transform: translate(-40px, 115px);
        }

        &__weight {
            width: 70px;
            height: 70px;
        }

        &__weightCount {
            font-size: 34px;
            line-height: 35px;
        }

        &__weightUnit {
            margin-top: 0;
            font-size: 18px;
        }
    }
}