// Импорт глобальных шрифтов
@import 'fonts/index.scss';

// Общие стили
* {
    box-sizing: border-box;
}

html, body {
    height: 100%;
    overflow: hidden;
}

body {
    margin: 0;
    color: #FFFFFF;
    font-family: 'Exo Thin', sans-serif;
    background: url(../static/images/background.jpg);
    background-size: cover;
}

h1, h2, h3, h4 {
    margin: 0;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}