// Глобальные миксины

// Основные размеры
@mixin screen-3 {
    @media screen and (max-width: #{$screen-3}) {
        @content;
    }
}

@mixin screen-5 {
    @media screen and (max-width: #{$screen-5}) {
        @content;
    }
}

@mixin screen-7 {
    @media screen and (max-width: #{$screen-7}) {
        @content;
    }
}

@mixin screen-12 {
    @media screen and (max-width: #{$screen-12}) {
        @content;
    }
}