@import 'styles/variables/_index.scss';
@import 'styles/mixins/_index.scss';

// Корневой элемент
.Root {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: 100%;
    overflow-y: auto;

    // Обертка для контента
    &__content {
        display: flex;
        flex: 1 0 auto;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 20px;
    }
    
    // Заголовок
    &__title {
        font-size: 36px;
        line-height: 48px;
    }

    // Список продукции
    &__products {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-top: 20px;
    }

    &__productsItem {
        display: flex;
        justify-content: center;
        margin-right: 80px;

        &:last-of-type {
            margin-right: 0;
        }
    }

    // Медиа запросы
    @include screen-12 {
        &__products {
            width: 100%;
        }

        &__productsItem {
            &:first-of-type {
                width: 100%;
                margin-right: 0;
                margin-bottom: 40px;
            }
        }
    }

    @include screen-7 {
        &__productsItem {
            width: 100%;
            margin-right: 0;
            margin-bottom: 40px;

            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }

    @include screen-5 {
        &__title {
            font-size: 26px;
            line-height: 30px;
        }
    }

    @include screen-3 {
        &__title {
            font-size: 21px;
            line-height: 20px;
        }

        &__products {
            width: 100%;
        }
    }
}