// Exo Thin
@font-face {
    font-family: 'Exo Thin';
    src: url('./Exo/Exo-Thin.eot');
    src: url('./Exo/Exo-Thin.eot?#iefix') format('embedded-opentype'),
         url('./Exo/Exo-Thin.woff') format('woff'),
         url('./Exo/Exo-Thin.otf') format('opentype'),
         url('./Exo/Exo-Thin.ttf') format('truetype');
    font-display: swap;
    font-style: normal;
}


